import React, { useState } from 'react';

const RecruitmentsEle = ({ ele, idx, handleSelectedList }) => {
  const [isChecked, setIsChecked] = useState(false);

  const handleChecked = e => {
    setIsChecked(!isChecked);
    handleSelectedList(e.target.value, isChecked);
  };

  return (
    <div
      className={`recruitment_modal_elements recuitment_modal_elements_${idx}`}
      key={idx}
    >
      <div className="recruitment_modal_checkboxarea">
        <input
          type="checkbox"
          key={`${ele}_${idx}`}
          name={`${ele}`}
          value={`${ele}`}
          // checked={isChecked}
          onChange={e => {
            handleChecked(e);
          }}
        />
      </div>

      <div className="recruitment_modal_content" key={`${idx}_${idx}`}>
        {ele}
      </div>
    </div>
  );
};

export default RecruitmentsEle;
